.card {
    width: 100px;
    height: 120px;
    background-color: rgb(158, 228, 255);
    color: rgb(158, 228, 255);
    border-radius: 12px;
    display: inline-flex;
    margin: 5px;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    transform: rotate3d(0, 1, 0, 180deg);
    transition-duration: 0.3s;
    user-select: none;
}

.card:hover {
    background-color: rgb(141, 223, 255);
    color: rgb(141, 223, 255);
    box-shadow: 1px 1px 10px rgb(211, 211, 211);

}

.clicked-card {

    color: black;
    transform: rotate3d(0, 1, 0, 0deg);
}

.clicked-card:hover {
    color: black;

}

.guessed-card {
    background-color: rgb(158, 255, 190);

    color: black;
    transform: rotate3d(0, 1, 0, 0deg);
}

.guessed-card:hover {
    background-color: rgb(139, 255, 178);

    color: black;
}